// ChatDetail.js -  
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
//
import RedMessage from "../../image/RedMessage.png"
// 
import TopBar from "../../components/TopBar";

// 
import { actionCreators as chatAction } from "../../redux/modules/chat";

const ChatDetail = (props) => {
  const [message, setMessage] = useState("");

  const dispatch = useDispatch();
  const list = useSelector((state) => state.chat.inBoxList[0]); //   
  const myId = localStorage.getItem("userId"); //   
  const chatId = props.match.params.chatId; //    
  // const receiverId = list.senderId; //    
  // const receiverNickname = list.senderNickname; //    
  // const receivedMessage = list.message; //   
  // const receivedTime = list.createdAt; //   

  const messageChange = (e) => {
    setMessage(e.target.value);
  };

  //     
  const sendChat = () => {
    dispatch(chatAction.sendMessageMD(list.senderId, message,1));
  };

  //     
  useEffect(() => {
    dispatch(chatAction.getDetailMD(chatId));
  }, []);

  return (
 
    <div>
      <TopBar></TopBar>
      <Info>
        <ImageWrap>
          <img src={list.senderImage}/>
          {list.senderNickname}
        </ImageWrap>
         <div>
           {list.AGOTIME}
         </div>
        </Info>
      <Wrap>
        <Message>
       
          <p>{list.message}</p>
    
        </Message>
        <SendInfo>
        <ImageWrap>
          <img src={RedMessage}/>
          {list.senderNickname} 
        </ImageWrap>
         <div>
         
         </div>
        </SendInfo>
        {/*            */}
        {list.receiverId !== myId && (
          <Input>
            <textarea
              type="text"
              placeholder="  "
              onChange={messageChange}
            />
          
          </Input>
        )}
      </Wrap>
      <SendBtn onClick={sendChat}></SendBtn>
    </div>
  );
};
const SendBtn = styled.div
`

display:flex;
justify-content:center;
box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
border: 1px solid lightGray;
width:30%;
height:2.5em;
align-items:center;
border-radius:15px;
margin: 30px auto;
`
const Info = styled.div
`
margin: 0 auto;
display:flex;
width:80%;
align-items:center;
justify-content:space-between;
margin-bottom:10px;
`
const SendInfo = styled.div
`
margin: 0 auto;
display:flex;
width:100%;
align-items:center;
justify-content:space-between;
margin-bottom:10px;
`
const ImageWrap = styled.div
`
display:flex;
align-items:center;
img{
  width:4rem;
   height:4rem;
border-radius: 50%;
object-fit: cover;
margin-right:10px;
}
margin-right:10px;
`
const Wrap = styled.div`
  display: block;

  width:80%;
  margin: 0 auto;
`;
const Message = styled.div`

  height: 40vh;
  width:100%;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid lightGray;
  border-radius: 30px;


  margin : 20px 0px;
  padding: 20px;
`;
const Input = styled.div`
  display: flex;

  width:100%;

  height: 40vh;

  padding: 20px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid lightGray;
  border-radius: 30px;
  margin-top:20px;
  textarea {
    width:100%;
    height: 200px;
    border: none;
    &:focus{
      outline:none;
    }
  }
 cursor:pointer;
  button {
    width: 50px;
    height: 30px;
  }
`;

export default ChatDetail;
